import { useState, useEffect } from "react"

import { getDuration } from "lib/getDuration"
import { WebData } from "store/tango"

import { useDevSettings } from "../../components/Settings/DevSettings"

export function useGameChallengeLogic(game: NonNullable<WebData["game"]>) {
  const [gameUrl, setGameUrl] = useState<string | null>(null)
  const [nextChallengeIn, setNextChallengeIn] = useState<number | null>(null)
  const [nextChallengeReady, setNextChallengeReady] = useState(false)
  const [startTimer, setStartTimer] = useState(false)
  const { devSettingsEnabled, devSettings } = useDevSettings()

  useEffect(() => {
    if (devSettingsEnabled) {
      setGameUrl(`${game.url}&challengeNumber=${devSettings.challengeNumber}`)
    } else if (!game.nextChallengeAt) {
      // The game does not support challenges, using default url
      setGameUrl(game.url)
    } else {
      const nextChallengeIn = getDuration(game.nextChallengeAt)

      if (nextChallengeIn < 0) {
        // The next challenge should already be available, but is delayed due to
        // Cloudflare caching, so we optimistically enable the next challenge,
        // but without the "Next challenge" label
        setGameUrl(`${game.url}&challengeNumber=${game.challengeId + 1}`)
      } else {
        // Normal flow, the next challenge is in the future, we start the timer
        setGameUrl(`${game.url}&challengeNumber=${game.challengeId}`)
        setNextChallengeIn(nextChallengeIn)
        setStartTimer(true)
      }
    }
  }, [
    devSettings.challengeNumber,
    devSettingsEnabled,
    game.challengeId,
    game.nextChallengeAt,
    game.url,
  ])

  useEffect(() => {
    if (!startTimer) return

    const handle = setInterval(() => {
      const nextChallengeIn = game.nextChallengeAt
        ? getDuration(game.nextChallengeAt)
        : null

      if (nextChallengeIn !== null) {
        setNextChallengeIn(nextChallengeIn)
        // If the time has come to play the next challenge, we will prompt the
        // user to refresh the page
        if (nextChallengeIn < 0) setNextChallengeReady(true)
      }
    }, 1000 * 30)

    return () => clearInterval(handle)
  }, [game.nextChallengeAt, startTimer])

  return { gameUrl, nextChallengeIn, nextChallengeReady }
}
