import { t } from "@lingui/macro"

import { staticTr, skipTr } from "./lingui"

export const getUserGameChallengeMsg = (
  gameTitle: string | undefined,
  gameScore: number | null
) => {
  const baseInfoMsg = gameTitle
    ? staticTr(t`Can you beat me at ${gameTitle}?`)
    : staticTr(t`Can you beat me at this game?`)
  const scoreInfoMsg = gameScore !== null && staticTr(t`I scored ${gameScore}`)

  return scoreInfoMsg
    ? skipTr(`${baseInfoMsg} ${scoreInfoMsg} `)
    : skipTr(`${baseInfoMsg} `)
}
