import { useEffect, useMemo } from "react"
import useFetch from "use-http"

import { normalizeGameUrl } from "./normalizeGameUrl"

export interface CLIData {
  cliVersion: string
  gameUrl: string
  gameTitle: string | null | undefined
  gameLogo: string | null | undefined
  gameBlurredImg: string | null | undefined
  playtest: boolean | undefined
  multiplayer: boolean | undefined
}

const queryParamsData = loadDataFromQueryParams()

export function useCLIData() {
  const { data, get } = useFetch<unknown>("/data")

  useEffect(() => {
    if (!queryParamsData) get()
  }, [get])

  return useMemo(() => {
    if (queryParamsData) return queryParamsData

    if (isCLIData(data)) {
      return {
        data: {
          ...data,
          gameUrl: normalizeGameUrl(data.gameUrl),
        },
      }
    }

    return { data: undefined }
  }, [data])
}

function isCLIData(data: any): data is CLIData {
  return data && typeof data === "object" && !!data.cliVersion && !!data.gameUrl
}

function booleanQueryArg(value: string | null) {
  return value ? ["1", "true"].includes(value) : undefined
}

function loadDataFromQueryParams(): { data: CLIData } | undefined {
  const params = new URLSearchParams(window.location.search)
  const gameUrl = params.get("gameUrl")

  return gameUrl
    ? {
        data: {
          cliVersion: "0.0.0",
          gameUrl: normalizeGameUrl(gameUrl),
          gameTitle: params.get("gameTitle"),
          gameLogo: params.get("gameLogo"),
          gameBlurredImg: params.get("gameBlurredImg"),
          playtest: booleanQueryArg(params.get("playtest")),
          multiplayer: booleanQueryArg(params.get("multiplayer")),
        },
      }
    : undefined
}
