import { useState, useEffect, useCallback } from "react"
import { GameOverContext } from "rune-games-sdk/multiplayer"

const defaultGameOverPopupDelay = 7000

export function useGameOverPopupVisibility(
  context: GameOverContext | undefined
) {
  const isGameOver = !!context
  const shouldDelayShowing =
    context?.reason === "gameEnded" && !!context.options?.delayPopUp

  const [gameDecidedToShow, setGameDecidedToShow] = useState(false)
  const [userDecidedToHide, setUserDecidedToHide] = useState(false)
  const [defaultTimeoutRanOut, setDefaultTimeoutRanOut] = useState(false)

  useEffect(() => {
    // cancelling timeout if the game triggered SHOW_GAME_OVER_POP_UP
    if (gameDecidedToShow) return

    if (isGameOver && shouldDelayShowing) {
      // starting a default timeout to show the popup
      const handle = setTimeout(() => {
        setDefaultTimeoutRanOut(true)
      }, defaultGameOverPopupDelay)

      return () => clearTimeout(handle)
    }
  }, [gameDecidedToShow, isGameOver, shouldDelayShowing])

  const onGameDecidedToShow = useCallback(() => {
    setGameDecidedToShow(true)
  }, [])

  const onUserDecidedToHide = useCallback(() => {
    setUserDecidedToHide(true)
  }, [])

  const visible =
    isGameOver &&
    (!shouldDelayShowing || gameDecidedToShow || defaultTimeoutRanOut) &&
    !userDecidedToHide

  useEffect(() => {
    setGameDecidedToShow(false)
    setUserDecidedToHide(false)
    setDefaultTimeoutRanOut(false)
  }, [context])

  return { visible, onGameDecidedToShow, onUserDecidedToHide }
}
