import React, { useCallback } from "react"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"

import { latencyOptions } from "../../AppMultiplayerCLI"
import { flex } from "../../lib/flex"
import { webTheme } from "../../styles/webTheme"
import { Modal } from "../Settings/Modal"

export function MultiplayerSettingsModal({
  latency,
  setLatency,
  delayJoin,
  setDelayJoin,
  onClose,
  onRestart,
}: {
  latency: number
  setLatency: (latency: number) => void
  delayJoin: boolean
  setDelayJoin: (delayJoin: boolean) => void
  onClose: () => void
  onRestart: () => void
}) {
  const onRestartClick = useCallback(() => {
    onRestart()
    onClose()
  }, [onClose, onRestart])

  return (
    <Modal onClose={onClose}>
      <Root>
        <button onClick={onRestartClick}>{skipTr("Restart game")}</button>
        <label>
          <span>{skipTr("Simulated latency:")}</span>
          <select value={latency} onChange={(e) => setLatency(+e.target.value)}>
            {latencyOptions.map((latency) => (
              <option key={latency} value={latency}>
                {skipTr(`${latency}ms`)}
              </option>
            ))}
          </select>
        </label>
        <label>
          <span>{skipTr("Simulate delayed joining:")}</span>
          <input
            type="checkbox"
            checked={delayJoin}
            onChange={(e) => setDelayJoin(e.target.checked)}
          />
        </label>
      </Root>
    </Modal>
  )
}

const Root = styled.div`
  padding: 20px 0;
  ${webTheme.text.label}
  ${flex({})};

  > :not(:first-child) {
    margin-top: 10px;
  }

  > label {
    ${flex({ horizontal: true, vAlign: "center" })};
    > :not(:first-child) {
      margin-left: 5px;
    }
  }
`
