import React, { useCallback, useEffect } from "react"
import { atom, useRecoilState } from "recoil"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"
import { WebData } from "store/tango"

import { webTheme } from "../../styles/webTheme"

import { Modal, Line, Text } from "./Modal"

interface DevSettingsState {
  game: NonNullable<WebData["game"]> | null
  sdkVersion: string | null
  cliVersion: string | null
  visible: boolean
  challengeNumber: number
  score: number | null
  scoreRequestTrigger?: number
  splitView: boolean
}

const $devSettings = atom<DevSettingsState>({
  key: "$devSettings",
  default: {
    game: null,
    sdkVersion: null,
    cliVersion: null,
    visible: false,
    challengeNumber: 1,
    score: null,
    scoreRequestTrigger: undefined,
    splitView: false,
  },
})

export function useDevSettings() {
  const [devSettings, setSettings] = useRecoilState($devSettings)
  const enabled = !!devSettings.game

  const setDevSettings = useCallback(
    (update: Partial<DevSettingsState>) => {
      setSettings((prev) => ({ ...prev, ...update }))
    },
    [setSettings]
  )

  const incrementDevChallengeNumber = useCallback(
    () =>
      setSettings((prev) => ({
        ...prev,
        challengeNumber: prev.challengeNumber + 1,
      })),
    [setSettings]
  )

  const decrementDevChallengeNumber = useCallback(
    () =>
      setSettings((prev) => ({
        ...prev,
        challengeNumber: prev.challengeNumber - 1,
      })),
    [setSettings]
  )

  return {
    devSettings,
    devSettingsEnabled: enabled,
    setDevSettings,
    incrementDevChallengeNumber,
    decrementDevChallengeNumber,
  }
}

export function DevSettings({ isDesktop }: { isDesktop: boolean }) {
  const {
    setDevSettings,
    incrementDevChallengeNumber,
    decrementDevChallengeNumber,
    devSettings: {
      visible,
      challengeNumber,
      sdkVersion,
      cliVersion,
      score,
      splitView,
    },
  } = useDevSettings()

  const hide = useCallback(() => setDevSettings({ visible: false }), [
    setDevSettings,
  ])

  useEffect(() => {
    if (visible) {
      // scoreRequestTrigger is used to avoid using event emitter or custom refs.
      // when the value changes, useEffect will trigger a score request in the GameView
      setDevSettings({ score: null, scoreRequestTrigger: Math.random() })
    }
  }, [setDevSettings, visible])

  const toggleSplitView = useCallback(() => {
    setDevSettings({ splitView: !splitView })
  }, [setDevSettings, splitView])

  if (!visible) return null

  return (
    <Modal onClose={hide}>
      <Line>
        <Text>{skipTr(`Score: ${score}`)}</Text>
      </Line>
      <Line>
        <Text>{skipTr(`Challenge:`)}</Text>
        <Button onClick={decrementDevChallengeNumber}>{skipTr("-")}</Button>
        <Text>{challengeNumber}</Text>
        <Button onClick={incrementDevChallengeNumber}>{skipTr("+")}</Button>
      </Line>
      {isDesktop && (
        <Line>
          <Text>{skipTr(`Split view:`)}</Text>
          <input
            type="checkbox"
            checked={splitView}
            onChange={toggleSplitView}
          />
        </Line>
      )}
      <Line>
        <Text>{skipTr(`SDK version: ${sdkVersion}`)}</Text>
      </Line>
      <Line>
        <Text>{skipTr(`CLI version: ${cliVersion}`)}</Text>
      </Line>
    </Modal>
  )
}

const Button = styled.div`
  ${webTheme.text.body};
  color: white;
  padding: 2px 8px;
  background-color: #444;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
`
