/*
Helper function to abbreviate a number.
*/

// TODO: Allow custom handling of fixed points.

import { localizeNumber } from "./localizeNumber"

export const abbreviateNumber = (number: number): string => {
  if (number > 1e6) {
    // 10M+, append 'M' (e.g. 103M)
    // 1-10M, append 'M' with 1 decimal (e.g. 1.2M)
    return `${localizeNumber((number / 1e6).toFixed(number > 1e7 ? 0 : 1))}M`
  }

  if (number > 1e3) {
    // 10K-1M, append 'K' (e.g. 11K)
    // 1K-10K, append 'K' with 1 decimal (e.g. 1.2K)
    return `${localizeNumber((number / 1e3).toFixed(number > 1e4 ? 0 : 1))}K`
  }

  return `${number}`
}
