import styled from "styled-components/macro"

import { flex } from "../../lib/flex"

import { GameState } from "./GameView"
import { ImgButton } from "./ImgButton"

export type BottomBarAction =
  | {
      imgSrc: string
      onClick?: (arg: {
        gameState: GameState
        onPause: () => void
        onPlay: () => void
        onRestart: () => void
      }) => void
      disabled?: boolean
    }
  | { spacer: true }

function isSpacer(action: BottomBarAction): action is { spacer: true } {
  return !!(action as any).spacer
}

export function BottomBar({
  gameState,
  onPause,
  onPlay,
  onRestart,
  disabled,
  actions,
}: {
  gameState: GameState
  onPause: () => void
  onPlay: () => void
  onRestart: () => void
  disabled: boolean
  actions: { playing: BottomBarAction[]; paused: BottomBarAction[] }
}) {
  return (
    <Root>
      {actions[
        gameState.matches("PLAYING") ||
        gameState.matches("LOADING.WAITING_TO_PLAY")
          ? "playing"
          : "paused"
      ].map((action, i) =>
        isSpacer(action) ? (
          <Spacer key={i} />
        ) : (
          <ImgButton
            key={action.imgSrc}
            src={action.imgSrc}
            onClick={() =>
              action.onClick?.({ gameState, onPause, onPlay, onRestart })
            }
            disabled={disabled || action.disabled}
          />
        )
      )}
    </Root>
  )
}

const Root = styled.div`
  height: 70px;
  background-color: #1c002b;
  ${flex({ horizontal: true, hAlign: "space-around", vAlign: "center" })};
`

const Spacer = styled.div`
  width: 40px;
  height: 40px;
`
