import { ReactNode } from "react"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"

import { flex } from "../../lib/flex"
import { webTheme } from "../../styles/webTheme"

export function Modal({
  onClose,
  children,
}: {
  onClose?: () => void
  children: ReactNode
}) {
  return (
    <Root onClick={onClose}>
      <Content onClick={(e) => e.stopPropagation()}>
        {!!onClose && (
          <CloseButton onClick={onClose}>{skipTr("")}</CloseButton>
        )}
        {children}
      </Content>
    </Root>
  )
}

const Root = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  ${flex({ vAlign: "center", hAlign: "center" })};
  background-color: rgba(0, 0, 0, 0.7);
`

const Content = styled.div`
  margin: 0 5%;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  > :not(:first-child) {
    margin-top: 5px;
  }
  position: relative;
`

const CloseButton = styled.div`
  position: absolute;
  top: 7px;
  right: 7px;
  cursor: pointer;
  color: ${webTheme.colors.default};
`

export const Line = styled.div`
  ${flex({ horizontal: true, vAlign: "center" })};
  > :not(:first-child) {
    margin-left: 5px;
  }
`

export const Text = styled.span`
  ${webTheme.text.body};
  color: #444;
`
