import {
  createMachine,
  MachineOptions,
  ServiceMap,
  BaseActionObject,
} from "xstate"

import { GamePlayEndType } from "store/validValues"

import { Typegen0 } from "./gameStateMachine.typegen"

type Context = {
  gamePlayUuid: string
}

export type MatchedGameStates = Typegen0["matchesStates"]

export type StateMachineEvents =
  | { type: "onReset" }
  | { type: "onInit" }
  | { type: "onFailToLoad" }
  | { type: "onPlay" }
  | { type: "onPause"; endType: GamePlayEndType }
  | { type: "onGameOver"; score: number; currentScore?: number }
  | { type: "onRestartImmediately" }
  | { type: "onRestartAnimationEnd" }
  | { type: "onError" }
  | { type: "onExitHighScore" }

// Latest state machine: https://stately.ai/registry/editor/fc6c117d-4f9b-4f06-8d5e-4d2ff416f766

//The typegen makes sure that all states exist. If you add a pointer to a state that does not exist the typegen will fail.
// NB: onRestartImmediately is internal event without target so that RESTART_GAME is triggered instead of Entry/Exit actions

export const gameStateMachine = createMachine(
  {
    //Next line gets auto updated.
    tsTypes: {} as import("./gameStateMachine.typegen").Typegen0,
    schema: {
      context: {} as Context,
      events: {} as StateMachineEvents,
    },
    context: {
      gamePlayUuid: "UNSET", // Game play uuid only makes sense while playing
    },
    preserveActionOrder: true, // Ensures that assign actions are called in order (see https://xstate.js.org/docs/guides/context.html#action-order and https://github.com/statelyai/xstate/issues/3383)

    // START - Exported JSON from state machine (don't remove newline below)

    id: "GAME_STATE (V8)",
    initial: "LOADING",
    states: {
      LOADING: {
        initial: "LOADING",
        states: {
          LOADING: {
            on: {
              onPlay: {
                target: "WAITING_TO_PLAY",
              },
              onInit: {
                actions: "GAME_READY",
                target: "#GAME_STATE (V8).PAUSED",
              },
              onFailToLoad: {
                target: "FAILED",
              },
            },
          },
          WAITING_TO_PLAY: {
            entry: "ENABLE_PAUSE_ANIMATION",
            on: {
              onFailToLoad: {
                target: "FAILED",
              },
              onInit: {
                actions: "GAME_READY",
                target: "#GAME_STATE (V8).PLAYING",
              },
              onPause: {
                target: "LOADING",
              },
            },
          },
          FAILED: {
            on: {
              onPlay: {
                actions: "RELOAD_FRAME",
                target: "WAITING_TO_PLAY",
              },
            },
          },
        },
      },
      PLAYING: {
        initial: "PLAYING",
        states: {
          PLAYING: {
            exit: ["REQUEST_SCORE", "END_GAME"],
            entry: ["ASSIGN_GAME_PLAY_UUID", "PLAY_GAME"],
            on: {
              onPause: {
                actions: "PAUSE_GAME",
                target: "#GAME_STATE (V8).PAUSED.PAUSED",
              },
              onGameOver: [
                {
                  cond: "IS_HIGH_SCORE",
                  target: "#GAME_STATE (V8).PAUSED.HIGH_SCORE",
                },
                {
                  target: "RESTARTING_ANIMATING",
                },
              ],
              onRestartImmediately: {
                actions: ["ASSIGN_GAME_PLAY_UUID", "RESTART_GAME"],
              },
            },
          },
          RESTARTING_ANIMATING: {
            on: {
              onRestartAnimationEnd: {
                target: "PLAYING",
              },
            },
          },
        },
      },
      PAUSED: {
        entry: "ENABLE_PAUSE_ANIMATION",
        initial: "READY",
        states: {
          HIGH_SCORE: {
            entry: "HIGH_SCORE_ENTERED",
            on: {
              onPlay: {
                target: "#GAME_STATE (V8).PLAYING",
              },
              onExitHighScore: {
                target: "PAUSED",
              },
            },
          },
          PAUSED: {
            on: {
              onPlay: {
                target: "#GAME_STATE (V8).PLAYING",
              },
            },
          },
          READY: {
            on: {
              onPlay: {
                target: "#GAME_STATE (V8).PLAYING",
              },
            },
          },
        },
      },
    },
    on: {
      onReset: {
        target: ".LOADING",
      },
      onError: {
        actions: "RELOAD_FRAME",
        target: ".LOADING",
      },
    },

    // END - Exported JSON from state machine (don't remove newline above)
  },
  {
    actions: {
      // Need to define all actions for type checking, later on we override the actions with bindGameContainerToStateMachine
      ASSIGN_GAME_PLAY_UUID: () => {},
      GAME_READY: () => {},
      PLAY_GAME: () => {},
      END_GAME: () => {},
      RESTART_GAME: () => {},
      RELOAD_FRAME: () => {},
      PAUSE_GAME: () => {},
      REQUEST_SCORE: () => {},
      HIGH_SCORE_ENTERED: () => {},
      ENABLE_PAUSE_ANIMATION: () => {},
    },
    guards: {
      IS_HIGH_SCORE: () => false,
    },
  }
)

type Config = Required<
  Pick<
    //Sadly this is the best way I was able to find to access the required actions/guards.
    MachineOptions<
      Context,
      StateMachineEvents,
      BaseActionObject,
      ServiceMap,
      Typegen0
    >,
    "actions" | "guards"
  >
>

export type GameStateMachineActions = Required<Config["actions"]>
export type GameStateMachineGuards = Required<Config["guards"]>
