import humanId from "human-id"
import range from "lodash/range"
import { encode, decode } from "rune-games-sdk/dist/common/serialization"
import {
  createGameServer,
  createRuneSDKForLogicRunner,
  RunGameLogic,
  isNewUserSpectator,
} from "rune-games-sdk/dist/multiplayer/gameServer"
import {
  ServerLogicActionContext,
  ServerLogicEventContext,
  Users,
} from "rune-games-sdk/multiplayer"
import * as uuid from "uuid"

import { generateAvatarUrl } from "../avatar"

import { emitter } from "./emitter"
import { createPromiseQueue } from "./queue"

export const users: Users = {}

// only call this after the game client state is PLAYING
export function isSpectator(userId: number) {
  return !users[userId].playerId
}

const queue = createPromiseQueue()

export async function startGameServer(gameUrl: string) {
  const logicJsUrl = new URL("logic.js", gameUrl).toString()

  const logicJs = await fetch(logicJsUrl).then((r) => r.text())

  const Rune = createRuneSDKForLogicRunner()

  eval(logicJs)

  const { setup, events, actions, gameConfig } = Rune

  if (!setup || !events || !actions || !gameConfig) {
    throw new Error("sdk init failed")
  }

  const logicRunner: RunGameLogic = {
    getConfig: () => gameConfig,
    runSetup: async (_, players, logicContext) => setup(players, logicContext),
    runEvent: async (_, logicContext, event, params, context) =>
      events[event]!(
        params,
        // Copy state so that game logic does not modify existing state inside the server
        decode(encode(context)) as ServerLogicEventContext,
        logicContext
      ),
    runAction: async (_, logicContext, action, params, actionContext) =>
      actions[action](
        params,
        // Copy state so that game logic does not modify existing state inside the server
        decode(encode(actionContext)) as ServerLogicActionContext,
        logicContext
      ),
  }

  const msgLogger = console

  function addOrUpdateUser({
    userId,
    isSpectator,
  }: {
    userId: number
    isSpectator: boolean
  }) {
    if (!isSpectator) {
      const playerId = uuid.v4()
      const existingUser = users[userId]

      users[userId] = {
        userId,
        playerId,
        displayName:
          (existingUser?.playerId && existingUser.displayName) ??
          humanId({
            adjectiveCount: 0,
            capitalize: false,
            separator: "-",
          }),
        avatarUrl:
          (existingUser?.playerId && existingUser.avatarUrl) ??
          generateAvatarUrl(),
      }
    } else {
      users[userId] = {
        userId,
        playerId: undefined,
      }
    }

    return users[userId].playerId
  }

  // pre-create users to match minPlayers, frontend will also pre-create player views
  range(0, gameConfig.minPlayers).map(() => {
    addOrUpdateUser({
      userId: Math.round(Math.random() * 1e5),
      isSpectator: false,
    })
  })

  function startServer({
    sendInitialStateSync,
  }: {
    sendInitialStateSync: boolean
  }) {
    return createGameServer({
      initialMsgLogger: msgLogger,
      gameId: 1,
      network: {
        getUsers: () => users,
        broadcastMsg: (_, msg) => emitter.emit("broadcastMsg", { msg }),
        sendMsg: (_, userId, msg) => {
          emitter.emit("emitMsg", { userId, msg })
        },
      },
      logic: logicRunner,
      initialServerData: undefined,
      serverSeed: Math.floor(Math.random() * Number.MAX_SAFE_INTEGER),
      sendInitialStateSync,
    })
  }

  let gameServer = await startServer({ sendInitialStateSync: false })

  emitter.addListener("connect", ({ userId }) => {
    queue(async () => {
      // in case this is a pre-created frontend player view trying to "connect"
      if (users[userId]) return

      const isSpectator = isNewUserSpectator(
        users,
        gameConfig,
        !!gameServer.getDataForSerialization().context.gameOver
      )
      const playerId = addOrUpdateUser({ userId, isSpectator })

      if (playerId) await gameServer.onPlayerJoined(msgLogger, playerId)
    })
  })

  emitter.addListener("disconnect", ({ userId }) => {
    queue(async () => {
      const playerId = users[userId].playerId
      delete users[userId]

      if (playerId) await gameServer.onPlayerLeft(msgLogger, playerId)
    })
  })

  emitter.addListener("onMsg", ({ userId, msg }) => {
    queue(async () => {
      await gameServer.onMsg(msgLogger, userId, msg)
    })
  })

  return {
    gameConfig,
    restartGame: () => {
      Object.keys(users).forEach((userId, i) => {
        addOrUpdateUser({
          userId: Number(userId),
          isSpectator: i >= gameConfig.maxPlayers,
        })
      })
      emitter.emit("restartingGame", {})

      startServer({ sendInitialStateSync: true }).then((newGameServer) => {
        gameServer = newGameServer
      })
    },
  }
}
