import React from "react"

import { AppMultiplayerCLI } from "./AppMultiplayerCLI"
import { AppSingleplayerCLI } from "./AppSingleplayerCLI"
import { useCLIData } from "./lib/useCLIData"

export function AppCLI() {
  const { data } = useCLIData()

  return data?.multiplayer ? (
    <AppMultiplayerCLI data={data} />
  ) : data ? (
    <AppSingleplayerCLI data={data} />
  ) : null
}
