import React, { useState, useEffect, useCallback } from "react"
import { useMediaQuery } from "react-responsive"
import { GameOverContext } from "rune-games-sdk/multiplayer"

import { useGameOverPopupVisibility } from "lib/useGameOverPopupVisibility"
import { Awaited } from "store/schema"

import { GameOverModal } from "./components/Multiplayer/GameOverModal"
import { MultiplayerViewDesktop } from "./components/Multiplayer/MultiplayerViewDesktop"
import { MultiplayerViewMobile } from "./components/Multiplayer/MultiplayerViewMobile"
import { GameErrorModal } from "./components/Settings/GameErrorModal"
import { emitter } from "./lib/server/emitter"
import { startGameServer, users } from "./lib/server/gameServer"
import { CLIData } from "./lib/useCLIData"
import { GameViewErrorHandler } from "./views/GameView/GameView"

export const latencyOptions = [0, 50, 300, 500, 2000]
export const maxPlayers = 4

export type PlayerView = {
  userId: number
  spectator?: boolean
}

export function AppMultiplayerCLI({ data }: { data: CLIData }) {
  const [gameServer, setGameServer] = useState<
    Awaited<ReturnType<typeof startGameServer>> | undefined
  >()
  const [latency, setLatency] = useState(
    () => +(localStorage.getItem("latency") ?? 300)
  )
  const [delayJoin, setDelayJoin] = useState(
    () => localStorage.getItem("delayJoin") === "true"
  )
  const [errorMessages, setErrorMessages] = useState<string[]>([])
  const isMobile = useMediaQuery({ query: "(max-width: 992px)" })
  const [gameOver, setGameOver] = useState<GameOverContext | false>(false)

  useEffect(() => {
    emitter.latency = latency
    localStorage.setItem("latency", `${latency}`)
  }, [latency])

  useEffect(() => {
    const delayJoinStored = localStorage.getItem("delayJoin") === "true"
    localStorage.setItem("delayJoin", `${delayJoin}`)
    if (delayJoinStored !== delayJoin) window.location.reload()
  }, [delayJoin])

  useEffect(() => {
    startGameServer(data.gameUrl).then(setGameServer)
  }, [data.gameUrl])

  const [playerViews, setPlayerViews] = useState<PlayerView[]>([])

  const addPlayerView = useCallback(() => {
    setPlayerViews((players) => [
      ...players,
      { userId: Math.round(Math.random() * 1e5) },
    ])
  }, [])

  const setPlayerViewSpectator = (i: number) => {
    playerViews[i].spectator = true
    setPlayerViews([...playerViews])
  }

  useEffect(() => {
    const unsubscribe = emitter.addListener("restartingGame", () => {
      setPlayerViews((playerViews) =>
        playerViews.map((playerView) => ({
          ...playerView,
          spectator: !users[playerView.userId].playerId,
        }))
      )
      setGameOver(false)
    })

    return () => unsubscribe()
  }, [])

  const onRemovePlayerView = (i: number) => {
    playerViews.splice(i, 1)
    setPlayerViews([...playerViews])
  }

  useEffect(() => {
    if (!gameServer?.gameConfig) return
    // pre-create player views for already pre-created users
    setPlayerViews(Object.values(users).map(({ userId }) => ({ userId })))
  }, [gameServer?.gameConfig])

  const onError = useCallback<GameViewErrorHandler>(
    (errMsg) => setErrorMessages((prev) => [...prev, errMsg]),
    []
  )

  const onErrorModalClose = useCallback(() => {
    setErrorMessages([])
  }, [])

  const onErrorModalReload = useCallback(() => {
    setErrorMessages([])
    window.location.reload()
  }, [])

  const onGameOver = useCallback((context: GameOverContext) => {
    setGameOver(context)
  }, [])

  const onRestart = useCallback(() => {
    gameServer?.restartGame()
  }, [gameServer])

  const gameOverPopUp = useGameOverPopupVisibility(gameOver || undefined)

  if (!gameServer?.gameConfig) return null

  const MultiplayerView = isMobile
    ? MultiplayerViewMobile
    : MultiplayerViewDesktop

  return (
    <>
      <MultiplayerView
        gameConfig={gameServer.gameConfig}
        latency={latency}
        setLatency={setLatency}
        delayJoin={delayJoin}
        setDelayJoin={setDelayJoin}
        playerViews={playerViews}
        data={data}
        addPlayerView={addPlayerView}
        onError={onError}
        onGameOver={onGameOver}
        onSpectator={setPlayerViewSpectator}
        onRemove={onRemovePlayerView}
        onShowGameOverPopUp={gameOverPopUp.onGameDecidedToShow}
        onRestart={onRestart}
      />
      <GameErrorModal
        messages={errorMessages}
        onClose={onErrorModalClose}
        onReload={onErrorModalReload}
      />
      {gameOver && gameOverPopUp.visible && (
        <GameOverModal
          gameConfig={gameServer.gameConfig}
          context={gameOver}
          onClose={gameOverPopUp.onUserDecidedToHide}
          onRestart={onRestart}
        />
      )}
    </>
  )
}
