import flow from "lodash/flow"

export const normalizeGameUrl = flow(
  (gameUrl: string) =>
    location.hostname === "localhost"
      ? gameUrl
      : gameUrl.replace("localhost", location.hostname),
  (gameUrl) => (gameUrl.endsWith("/") ? gameUrl : `${gameUrl}/`),
  (gameUrl) =>
    gameUrl.endsWith("index.html/")
      ? gameUrl.slice(0, -"index.html/".length)
      : gameUrl,
  (gameUrl) => (gameUrl.endsWith(".html/") ? gameUrl.slice(0, -1) : gameUrl)
)
