import { useCallback } from "react"


import { getGameChallengeLinkMsg } from "lib/getGameChallengeLinkMsg"
import { WebData } from "store/tango"

import { webLogger } from "./logWeb"

export const shareSupported = !!navigator.share

export function useShareMenu(game: WebData["game"]) {
  return {
    openShareMenu: useCallback(() => {
      navigator
        .share({
          text: getGameChallengeLinkMsg({
            gameTitle: game?.title ?? "",
            gameScore: null,
            gameShareLink: location.href,
          }),
        })
        .then(() =>
          webLogger.info({
            action: "webAppShareLink",
            msg: "Web share link interaction",
            info: { type: "success", game },
          })
        )
        .catch(() =>
          webLogger.info({
            action: "webAppShareLink",
            msg: "Web share link interaction",
            info: { type: "fail", game },
          })
        )
    }, [game]),
  }
}
