import { data } from "./data"

const gameRouteMatch = location.pathname.match(
  /^\/[a-zA-Z0-9-]+-(\w+)-(\w+)\/?(\?.*)?$/
)
const linkKey = gameRouteMatch?.[2]

export const webConfig = {
  route: { isGamePath: !!gameRouteMatch, linkKey },
  installRuneUrl: installRuneUrl(detectStage()),
  playStoreUrl: "https://play.google.com/store/apps/details?id=ai.rune.tincan",
  appStoreUrl:
    "https://apps.apple.com/us/app/rune-teammates-voice-chat/id1450358364",
  stage: detectStage(),
}

export type Stage = "staging" | "launchpad" | "production"

function detectStage(): Stage {
  switch (location.host) {
    case "app-launchpad.rune.ai":
      return "launchpad"
    case "app.rune.ai":
      return "production"
    default:
      return "staging"
  }
}

function installRuneUrl(stage: Stage) {
  const queryParams = `linkKey=${linkKey}${
    data.game?.id ? `&openGameId=${data.game.id}` : ""
  }`

  switch (stage) {
    case "staging":
      return {
        desktop: `https://join-alpha.rune.ai/uPWyxoRGRnb?${queryParams}`,
        mobile: `https://join-alpha.rune.ai/Rt8oSaGGRnb?${queryParams}`,
      }
    case "launchpad":
      return {
        desktop: `https://join-beta.rune.ai/qj001k2GRnb?${queryParams}`,
        mobile: `https://join-beta.rune.ai/5W5LHq1GRnb?${queryParams}`,
      }
    case "production":
      return {
        desktop: `https://join.rune.ai/QynEF1dHRnb?${queryParams}`,
        mobile: `https://join.rune.ai/H2waV49GRnb?${queryParams}`,
      }
  }
}
