import { t } from "@lingui/macro"
import moment from "moment"

import { staticTr } from "./lingui"

export function formatNextChallengeIn(nextChallengeIn: number) {
  return staticTr(
    t`New challenge in ${moment
      // Limit the resolution to "in a minute" since tango runs lambda every minute
      .duration(Math.max(nextChallengeIn, 60 * 1000), "milliseconds")
      .humanize()}`
  )
}
