import EventEmitter from "events"

const _emitter = new EventEmitter()

type Events = {
  emitMsg: { userId: number; msg: string }
  broadcastMsg: { msg: string }
  connect: { userId: number }
  disconnect: { userId: number }
  onMsg: { userId: number; msg: string }
  restartingGame: {}
}

export const emitter = {
  latency: 0,
  emit<E extends keyof Events>(event: E, payload: Events[E]) {
    setTimeout(() => _emitter.emit(event, payload), this.latency)
  },
  addListener<E extends keyof Events>(
    event: E,
    callback: (payload: Events[E]) => void
  ) {
    _emitter.addListener(event, callback)

    return () => {
      _emitter.removeListener(event, callback)
    }
  },
}
