import { compare } from "compare-versions"
import React, {
  useMemo,
  useCallback,
  useEffect,
  useState,
  useReducer,
  useRef,
} from "react"
import { useMediaQuery } from "react-responsive"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"

import gameRestartImg from "../../../assets/img/icon/gameRestart.png"
import gamesLightImg from "../../../assets/img/icon/gamesLight.png"
import musicOffRedImg from "../../../assets/img/icon/musicOffRed.png"
import musicOnLightImg from "../../../assets/img/icon/musicOnLight.png"
import pauseLightImg from "../../../assets/img/icon/pauseLight.png"
import settingsLightImg from "../../../assets/img/icon/settingsLight.png"

import { DevSettings, useDevSettings } from "./components/Settings/DevSettings"
import { GameErrorModal } from "./components/Settings/GameErrorModal"
import { HighScoreModal } from "./components/Settings/HighScoreModal"
import { PlaytestLinkWarningModal } from "./components/Settings/PlaytestLinkWarningModal"
import { flex } from "./lib/flex"
import { CLIData } from "./lib/useCLIData"
import { webTheme } from "./styles/webTheme"
import { BottomBarAction } from "./views/GameView/BottomBar"
import {
  GameView,
  GameViewErrorHandler,
  GameViewRef,
} from "./views/GameView/GameView"

const minSdkVersion = "2.1.0"

export function AppSingleplayerCLI({ data }: { data: CLIData }) {
  const { devSettings, setDevSettings } = useDevSettings()
  const isDesktop = useMediaQuery({ query: "(min-width: 992px)" })
  const gameViewRef = useRef<GameViewRef>(null)

  const [errorMessages, setErrorMessages] = useState<string[]>([])
  const [gameViewId, rerenderGameView] = useReducer((state) => state + 1, 0)
  const [userPrefersMuted, setUserPrefersMuted] = useState(false)
  const [highScore, setHighScore] = useState(0)
  const [playtestLinkWarning, setPlaytestLinkWarning] = useState(false)

  useEffect(() => {
    setDevSettings({
      cliVersion: data.cliVersion,
      game: {
        id: 1,
        challengeId: 1,
        nextChallengeAt: null,
        url: `${data.gameUrl}?enableInitialOverlayInBrowser=1&startMuted=1`,
        blurredImg:
          data.gameBlurredImg ??
          "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNs+A8AAgUBgQvw1B0AAAAASUVORK5CYII=",
        logoUrl: data.gameLogo ?? gamesLightImg,
        title: data.gameTitle ?? "Game Title",
        playCount: 99000,
        commentCount: 99000,
      },
    })
  }, [data, setDevSettings])

  const openSettings = useCallback(() => setDevSettings({ visible: true }), [
    setDevSettings,
  ])

  useEffect(() => {
    if (devSettings.visible) gameViewRef.current?.pause()
  }, [devSettings.visible])

  const bottomBarActions = useMemo<{
    playing: BottomBarAction[]
    paused: BottomBarAction[]
  }>(
    () => ({
      playing: [
        { imgSrc: gameRestartImg, onClick: ({ onRestart }) => onRestart() },
        { imgSrc: pauseLightImg, onClick: ({ onPause }) => onPause() },
        {
          imgSrc: userPrefersMuted ? musicOffRedImg : musicOnLightImg,
          onClick: () => setUserPrefersMuted(!userPrefersMuted),
        },
        { imgSrc: settingsLightImg, onClick: openSettings },
      ],
      paused: [
        {
          imgSrc: gameRestartImg,
          onClick: ({ onRestart }) => onRestart(),
          disabled: true,
        },
        { imgSrc: pauseLightImg, disabled: true },
        {
          imgSrc: userPrefersMuted ? musicOffRedImg : musicOnLightImg,
          disabled: true,
        },
        { imgSrc: settingsLightImg, onClick: openSettings },
      ],
    }),
    [userPrefersMuted, openSettings]
  )

  const setSdkVersion = useCallback(
    (sdkVersion: string) => setDevSettings({ sdkVersion }),
    [setDevSettings]
  )

  const sdkVersionSupported = useMemo(
    () =>
      !!devSettings.sdkVersion &&
      compare(devSettings.sdkVersion, minSdkVersion, ">="),
    [devSettings.sdkVersion]
  )

  const setScore = useCallback((score: number) => setDevSettings({ score }), [
    setDevSettings,
  ])

  const onError = useCallback<GameViewErrorHandler>(
    (errMsg) => setErrorMessages((prev) => [...prev, errMsg]),
    []
  )

  const onErrorModalClose = useCallback(() => {
    setErrorMessages([])
  }, [])

  const onErrorModalReload = useCallback(() => {
    setErrorMessages([])
    rerenderGameView()
  }, [])

  const onHighScoreRestart = useCallback(() => {
    gameViewRef.current?.play()
  }, [])

  useEffect(() => {
    if (data?.playtest) setPlaytestLinkWarning(true)
  }, [data?.playtest])

  const hidePlaytestLinkWarning = useCallback(
    () => setPlaytestLinkWarning(false),
    []
  )

  useEffect(() => {
    rerenderGameView()
  }, [devSettings.challengeNumber])

  if (!devSettings.game) return null

  if (devSettings.sdkVersion && !sdkVersionSupported) {
    return (
      <Root>
        <Disclaimer>
          {skipTr(`Your game uses Rune SDK version ${devSettings.sdkVersion}.`)}
          <br />
          {skipTr(
            `This CLI tool only supports versions ${minSdkVersion} or higher.`
          )}
          <br />
          {skipTr(`Please update the SDK version in your index.html.`)}
        </Disclaimer>
      </Root>
    )
  }

  const gameView = (
    <GameView
      ref={gameViewRef}
      key={gameViewId}
      game={devSettings.game}
      isPortrait
      shouldRenderIframe
      bottomBarActions={bottomBarActions}
      onSdkVersionReported={setSdkVersion}
      scoreRequestTrigger={devSettings.scoreRequestTrigger}
      onScore={setScore}
      onError={onError}
      userPrefersMuted={userPrefersMuted}
      currentScore={highScore}
      onNewHighScore={setHighScore}
    />
  )

  return (
    <>
      <Root>
        {isDesktop && (
          <Disclaimer>
            {skipTr(
              `It looks like you're using a desktop device. The game might not be displaying correctly. Please use a mobile device simulator in Chrome, an iOS or Android simulator, or a physical device.`
            )}
          </Disclaimer>
        )}
        {isDesktop ? (
          devSettings.splitView ? (
            <PhoneContainers>
              <PhoneContainer>{gameView}</PhoneContainer>
              <PhoneContainerBigWrapper>
                <PhoneContainerBig>{gameView}</PhoneContainerBig>
              </PhoneContainerBigWrapper>
            </PhoneContainers>
          ) : (
            <PhoneContainer>{gameView}</PhoneContainer>
          )
        ) : (
          gameView
        )}
      </Root>
      <DevSettings isDesktop={isDesktop} />
      <GameErrorModal
        messages={errorMessages}
        onClose={onErrorModalClose}
        onReload={onErrorModalReload}
      />
      <HighScoreModal highScore={highScore} onRestart={onHighScoreRestart} />
      {playtestLinkWarning && (
        <PlaytestLinkWarningModal onClose={hidePlaytestLinkWarning} />
      )}
    </>
  )
}

const Root = styled.div`
  width: 100%;
  height: 100%;
  ${flex({ vAlign: "center", hAlign: "center" })};
  background-color: grey;
  > :not(:first-child) {
    margin-top: 20px;
  }
`

const Disclaimer = styled.div`
  ${webTheme.text.body};
  color: white;
  max-width: 500px;
  text-align: center;
`

const PhoneContainers = styled.div`
  ${flex({ horizontal: true })};
  > :not(:first-child) {
    margin-left: 10px;
  }
`

const PhoneContainer = styled.div`
  width: 375px;
  height: 667px;
  border: 10px solid black;
  border-radius: 20px;
  box-sizing: content-box;
  overflow: hidden;
`

const PhoneContainerBigWrapper = styled.div`
  width: 245px;
  height: 485px;
`

const PhoneContainerBig = styled(PhoneContainer)`
  width: 798px;
  height: 1694px;
  transform: scale(0.4);
  transform-origin: top left;
`
