/*
Construct game challenge link msg
*/

import { getUserGameChallengeMsg } from "./getUserGameChallengeMsg"
import { skipTr } from "./lingui"

type GetGameChallengeLinkMsgProps = {
  gameTitle: string
  gameScore: number | null
  gameShareLink: string
}

export const getGameChallengeLinkMsg = ({
  gameTitle,
  gameScore,
  gameShareLink,
}: GetGameChallengeLinkMsgProps) =>
  skipTr(
    `${``} ${getUserGameChallengeMsg(gameTitle, gameScore)} ${gameShareLink}`
  )
