import React, { useCallback } from "react"
import { GameOverContext, GameConfig } from "rune-games-sdk/multiplayer"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"

import { flex } from "../../lib/flex"
import { webTheme } from "../../styles/webTheme"
import { Modal } from "../Settings/Modal"

export function GameOverModal({
  gameConfig,
  context,
  onClose,
  onRestart,
}: {
  gameConfig: GameConfig
  context: GameOverContext
  onClose: () => void
  onRestart: () => void
}) {
  const getDisplayNameFromPlayerId = useCallback(
    (playerId: string) => context.players[playerId].displayName,
    [context.players]
  )

  const onRestartClick = useCallback(() => {
    onRestart()
    onClose()
  }, [onClose, onRestart])

  return (
    <Modal onClose={onClose}>
      <Root>
        <Header>{skipTr("Game Over")}</Header>
        {context.reason === "playerLeft" ? (
          <Body>
            {skipTr(
              "Your game doesnt allow players to leave mid-game as it has not defined a callback for the playerLeft event. Rune will prompt the remaining roommates to restart the game. If youd like to support allow continuing the game when a player leaves midway, please define a callback for the playerLeft event"
            )}
          </Body>
        ) : context.reason === "minPlayers" ? (
          <Body>
            {skipTr(
              `The number of roommates is below minPlayers (${gameConfig.minPlayers}). When this happens, Rune will automatically end the game. Rune will prompt users to invite a friend or change the game. If youd like to support continuing to play with less than ${gameConfig.minPlayers}, change your value for minPlayers in logic.js and ensure your game works well with fewer players.`
            )}
          </Body>
        ) : context.reason === "err" ? (
          <Body>
            {skipTr(`An unexpected error happened (see console for details).`)}
          </Body>
        ) : context.options ? (
          <Body>
            {Object.keys(context.options.players).map((playerId) => (
              <div key={playerId}>
                {skipTr(
                  `${getDisplayNameFromPlayerId(playerId)}: ${
                    context.options?.players[playerId]
                  }`
                )}
              </div>
            ))}
          </Body>
        ) : null}
        <Buttons>
          {context.reason === "gameEnded" ? (
            <Button onClick={onRestartClick}>{skipTr("Restart game")}</Button>
          ) : (
            <Button onClick={() => window.location.reload()}>
              {skipTr("Reload page")}
            </Button>
          )}
        </Buttons>
      </Root>
    </Modal>
  )
}

const Root = styled.div`
  ${flex({ hAlign: "center" })};
  max-width: 600px;
  > :not(:first-child) {
    margin-top: 10px;
  }
`

const Header = styled.div`
  ${webTheme.text.headingBold};
`

const Body = styled.div`
  ${webTheme.text.body};
  margin-bottom: 10px;
`

export const Button = styled.div`
  padding: 5px 10px;
  border-radius: 5px;
  ${webTheme.text.body};
  color: white;
  background-color: grey;
  cursor: pointer;
`

const Buttons = styled.div`
  ${flex({ horizontal: true })};
  > :not(:first-child) {
    margin-left: 10px;
  }
`
